import { createAction } from '@reduxjs/toolkit'
import { Country } from 'state/cc/actions'

export interface Proxy {
  method: string
  type: string
  host: string
  port: string
  username?: string
  password?: string
}

export interface ProxyType { 
  label: string
  value: string 
}

export interface FingerPrint {
	ostype: string              //  PC|Android
  os: string                  // Win64|Win32|Linux i686|Linux armv7l|MacIntel
  version: string             //[77,78,...,96]
  userAgent: string           // 
  isIpCreateTimeZone: string  // IP
  timeZone: string            // isIpCreateTimeZonefalse
  webRTC: string,             // webrtc 0()|1()|2()
  position: string,           //  0()|1()|2()
  isIpCreatePosition: string  // IP
  lat: string                 //  isIpCreatePositionfalse
  lng: string                 //  isIpCreatePositionfalse
  precisionData: string       //  isIpCreatePositionfalse
  isIpCreateLanguage: string  // IP
  languages: string           // isIpCreateLanguagefalse
  resolutionType: string      //  0()|1()
  resolution: string          //  1920 x 1080 | 1920 x 1080 | 800 x 600 | ...
  fontType: string            //  0() | 1() | 2()
  font: string                // fontType
  canvas: string              // 0() | 1()
  webGL: string               // webGL 0() | 1()
  webGLMeta: string           // webGL 0() | 1()
  webGLManufacturer: string   // webGL
  webGLRender: string         // webGLlist
  audioContext: string        // Audio 0() | 1()
  mediaDevice: string         // Media 0() | 1()
  hardwareConcurrency: string // CPU
  deviceMemory: string        //
  doNotTrack: string          // 0() | 1()
  colorDepth: string          //  [48,32, 30, 24, 18, 16, ...] 
}

export interface Task {
  id: string
  name: string
  platform: string
  url: string
  price: string
  remark: string
  proxy: Proxy
  proxyUrl?: string
  browserFingerPrint: FingerPrint,
  group: string,
  country?: Country,
  createdTime?: number
}

export const addTask = createAction<{ accountId: string; task: Task }>('task/add')
export const removeTask = createAction<{ accountId: string; task: Task }>('task/remove')
